import {SERVICE_REQUEST, SERVICE_SUCCESS, SERVICE_FAILURE} from '../actions/types';

const initialState = {
	services: [],
	isLoading: false,
	isError: false
};

export default (state = initialState, action) => {
	switch (action.type) {
		case SERVICE_REQUEST:
			return {
				...state,
				...action.value
			};
		case SERVICE_SUCCESS:
			return {
				...state,
				...action.value
			};
		case SERVICE_FAILURE:
			return initialState;
		default:
			return state;
	}
};
