import 'babel-polyfill';
import React from 'react';
import {AppContainer} from 'react-hot-loader';
import {Route, Switch} from 'react-router-dom';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';

// redux
import {Provider} from 'react-redux';
import {ConnectedRouter} from 'connected-react-router';

// Constants
import {URLS} from './Constants';

// Layouts
import App from './App';

// Store
import configureStore, {history} from './configureStore';

// containers
import Main from 'containers/Main';
import Contacts from 'containers/Contacts';
import Case from 'containers/Case';
import Services from 'containers/Services';
import About from 'containers/About';
import Approach from 'containers/Approach';
import CardDescription from 'containers/CardDescription';
import TestContainer from 'containers/TestContainer';

import { TransitionGroup, CSSTransition } from 'react-transition-group';

const store = configureStore();

const AppRoute = ({ component: Component, layout: Layout, isDark: isDark, animationName: animationName}) => (
	<Route
		render={props => (
			<Layout isDark={isDark} animationName={animationName}>
				<Component {...props} />
			</Layout>
		)}
	/>
);


const render = () => {
	ReactDOM.render(
		<AppContainer>
			<Provider store={store}>
				<ConnectedRouter history={history}>
					<Route render={({ location }) => (
						<TransitionGroup className="transition-group">
							<CSSTransition
								key={location.pathname}
								timeout={{ enter: 1500, exit: 1500 }}
								classNames={'page'}
							>
								<Switch key={location.key} location={location}>
									<AppRoute
										exact
										isDark={false}
										path={URLS.case}
										layout={App}
										component={Case}
										animationName={'mainAnimation'}
									/>
									<AppRoute
										exact
										isDark={false}
										path={`${URLS.case}/:id`}
										layout={App}
										component={CardDescription}
										animationName={'servicesAnimation'}
									/>
									<AppRoute
										exact
										isDark
										path={URLS.approach}
										layout={App}
										component={Approach}
										animationName={'approachAnimation'}
									/>
									<AppRoute
										exact
										isDark
										path={URLS.about}
										layout={App}
										component={About}
										animationName={'aboutAnimation'}
									/>
									<AppRoute
										exact
										isDark
										path={URLS.services}
										layout={App}
										component={Services}
										animationName={'servicesAnimation'}
									/>
									<AppRoute
										exact
										isDark
										path={URLS.contacts}
										layout={App}
										component={Contacts}
										animationName={'contactsAnimation'}
									/>
									<AppRoute
										exact
										path={URLS.test}
										layout={App}
										component={TestContainer}
									/>
									<AppRoute
										exact
										isDark={false}
										path={URLS.index}
										layout={App}
										component={Main}
										animationName={'caseAnimation'}
									/>
								</Switch>
							</CSSTransition>
						</TransitionGroup>
					)} />
				</ConnectedRouter>
			</Provider>
		</AppContainer>,
		document.getElementById('root')
	);
};

render();

// Hot reloading
// if (module.hot) {
// // Reload components
// 	module.hot.accept('./App', () => {
// 		render();
// 	});
// }

AppRoute.propTypes = {
	animationName: PropTypes.string,
	component: PropTypes.func,
	layout: PropTypes.func,
	isDark: PropTypes.bool
};
