import { SERVICE_REQUEST, SERVICE_SUCCESS } from '../actions/types';
import services from '../mocks/services';

export function fetchServiceData() {
	return (dispatch) => {
		dispatch({
			type: SERVICE_REQUEST,
			value: {
				isLoading: true,
				isError: false
			}
		});
		dispatch({
			type: SERVICE_SUCCESS,
			value: {
				services,
				isLoading: false,
				isError: false
			}
		});
	};
}
