import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import {Link} from 'react-router-dom';
import {menu} from 'mocks/menu';

// components
import Row from '../grid/Row';
import Col from '../grid/Col';
import Menu from './Menu';
import { CSSTransition } from 'react-transition-group';


class Header extends Component {
	constructor(props) {
		super(props);
		const { isDark, showMenu } = this.props;
		const logoImg = (showMenu || isDark) ? '/assets/images/cm_logo_2_w.svg' : '/assets/images/cm_logo_2.svg';
		this.state = {
			logo: logoImg,
			count: 0
		};
	}


	setLogo() {
		const { isDark, showMenu, color } = this.props;
		const logoImg2 = (showMenu || isDark || !color) ? '/assets/images/cm_logo_2_w.svg' : '/assets/images/cm_logo_2.svg';
		const logoImg3 = (showMenu || isDark || !color) ? '/assets/images/cm_logo_3_w.svg' : '/assets/images/cm_logo_3.svg';
		const images = [logoImg2, logoImg3];
		const length = images.length;

		this.setState({
			count: this.state.count + 1,
			logo: images[this.state.count]
		});

		if(this.state.count >= length - 1) {
			this.setState({ count: 0 });
		}
	}


	render() {
		const { className, isDark, showMenu, closeMenu, showMenuFunc, color } = this.props;
		const linkClasses = classNames('header-link magnetize', className, { 'active': (showMenu || !color) });
		const mainClass = classNames('header', { 'active': showMenu, 'dark': !color });
		const menuTitle = showMenu ? 'Ð²ÐµÑÐ½ÑÑÑÑÑ' : 'Ð¼ÐµÐ½Ñ';
		const logoImg1 = (showMenu || isDark || !color) ? '/assets/images/cm_logo_1_w.svg' : '/assets/images/cm_logo_1.svg';
		// const logoImg2 = (showMenu || isDark) ? '/assets/images/cm_logo_2_w.svg' : '/assets/images/cm_logo_2.svg';
		// const logoImg3 = (showMenu || isDark) ? '/assets/images/cm_logo_3_w.svg' : '/assets/images/cm_logo_3.svg';

		return (
			<Fragment>
				<header className={mainClass}>
					<Row className="middle-xs between-xs">
						<Link className={(showMenu || isDark) ? 'flip-card dark' : 'flip-card'} onClick={closeMenu} to="/" >
							<div className="magnetize">
								<div className="flip-card-inner" onMouseEnter={() => this.setLogo()}>
									{/* <img src={logoImg3} alt="Cloudmill" /> */}
									<div className="flip-card-front"><img src={logoImg1} alt="Cloudmill" /></div>
									<div className="flip-card-back"><img src={this.state.logo} alt="Cloudmill" /></div>
								</div>
							</div>
						</Link>
						<Col>
							<a className={linkClasses} onClick={showMenuFunc}>{menuTitle}</a>
						</Col>
					</Row>
				</header>
				<CSSTransition
					in={showMenu}
					timeout={1000}
					classNames="scale"
					unmountOnExit
				>
					<Menu items={menu} ifClicked={closeMenu}/>
				</CSSTransition>
			</Fragment>
		);
	}
}

Header.propTypes = {
	className: PropTypes.string,
	isDark: PropTypes.bool,
	color: PropTypes.bool,
	showMenu: PropTypes.bool,
	showMenuFunc: PropTypes.func,
	closeMenu: PropTypes.func
};

export default Header;
