import React from 'react';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';
import Typography from '../markup/Typography';

const LinkBlock = (props) => {
	const mainClass = `indexPage--backCase frame indexPage-aboutLink hoverLink ${props.class}`;
	return (
		<Link to={props.link} className={mainClass} onClick={props.onClick}>
			<div className={'container hoverLink'}>
				<div className={'col-md-7 col-xs-12 center-block hoverLink'}>
					<Typography tag={'h1'} className={'hoverLink'} text={props.text} />
				</div>
			</div>
		</Link>
	);
};

LinkBlock.propTypes = {
	link: PropTypes.string,
	onClick: PropTypes.func,
	text: PropTypes.string,
	class: PropTypes.string
};

export default LinkBlock;
