import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';

import { fetchCatalogData } from 'actions/CatalogActions';
import { fetchServiceData } from 'actions/ServiceActions';

import { Services } from './Services';

const mapStateToProps = (state) => ({
	catalog: state.catalog,
	services: state.services
});

const mapDispatchToProps = dispatch => ({
	fetchServiceData: bindActionCreators(fetchServiceData, dispatch),
	fetchCatalogData: bindActionCreators(fetchCatalogData, dispatch)
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Services));
