import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {Helmet} from 'react-helmet';

// components
import CardComponent from 'components/catalog/CardComponent';
import LoadDocument from 'components/animate/LoadDocument';

export class CardDescription extends Component {

	constructor(props) {
		super(props);
		this.updateCard = this.updateCard.bind(this);
	}

	componentDidMount() {
		this.updateCard();
	}
	componentDidUpdate(prevProps) {
		if(this.props.location.pathname !== prevProps.location.pathname) {
			this.updateCard();
		}
	}

	updateCard() {
		this.props.fetchCatalogData();
		const pathname = this.props.location.pathname.replace('/projects/', '');
		const numb = parseInt(pathname, 0);
		this.props.setCurrentItem(numb - 1);
	}

	render() {
		const {catalog, isLoading, current} = this.props.catalog;
		return (
			<section className="cardContainer">
				<Helmet>
					<link rel="canonical" href={`http://cloudmill.ru/projects/${current + 1}`} />
				</Helmet>
				{isLoading ? (
					<LoadDocument />
				) : (
					((current !== null && !isNaN(current)) && catalog.length > 0) && <CardComponent item={catalog[current].case}/>
				)}
			</section>
		);
	}
}

CardDescription.propTypes = {
	fetchCatalogData: PropTypes.func,
	setCurrentItem: PropTypes.func,
	catalog: PropTypes.object,
	location: PropTypes.object
};


