import { OPEN_POPUP, CLOSE_POPUP } from '../actions/types';

const initialState = {
	popupName: '',
	title: '',
	description: '',
	buttons: []
};

export default (state = initialState, action) => {
	switch (action.type) {
		case OPEN_POPUP:
			return {
				popupName: action.popup,
				title: action.title,
				description: action.description,
				buttons: action.buttons
			};
		case CLOSE_POPUP:
			return initialState;
		default:
			return state;
	}
};
