import React, {Component} from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import { ParallaxProvider } from 'react-scroll-parallax';

// redux
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';

// actions
import { countLoading} from 'actions/LoadingActions';

// components
import Header from 'components/partials/Header';
import Loading from 'components/animate/Loading';
import Linker from 'components/partials/Linker';
import Cursor from 'components/partials/Cursor';
import Popup from 'components/popups/index';

// utils
import { getElementOffset } from 'utils/getElement';
import { Magnetize } from 'utils/Magnet';

// styles
import 'assets/styles/app.scss';

class App extends Component {
	constructor(props) {
		super(props);
		const { isDark, loading } = this.props;
		this.state = {
			loading: loading.count === 0,
			color: !isDark,
			colorBottom: !isDark,
			mouseX: 0,
			mouseY: 0,
			touchClass: '',
			clicked: false,
			isWhiteCursor: false,
			isHover: false,
			showMenu: false
		};
	}

	componentDidMount() {
		Magnetize();
		this.isLoad();
		window.scrollTo(0, 0);
		window.addEventListener('scroll', this.handleScroll.bind(this));
	}

	componentWillUnmount() {
		const { isDark } = this.props;
		this.setState({
			color: !isDark
		});
		window.removeEventListener('scroll', this.handleScroll.bind(this));
	}

	onMouseMove(event) {
		const isLinkTag = event.target.tagName.toLowerCase() === 'a';
		const isLinkClass = event.target.classList.contains('hoverLink');
		this.setState({
			mouseX: event.clientX,
			mouseY: event.clientY + 60,
			isHover: isLinkTag || isLinkClass,
			isWhiteCursor: isLinkClass
		});
	}

	onMouseDown() {
		this.setState({ touchClass: 'touchClass', clicked: true });
		setTimeout(() => {
			this.setState({ clicked: false });
		}, 100);
	}

	onMouseUp() {
		this.setState({ touchClass: ''});
	}

	handleScroll() {
		const elemBlue = document.getElementsByClassName('indexPage--blue')[0] && getElementOffset(document.getElementsByClassName('indexPage--blue')[0]);
		const elemWhite = document.getElementsByClassName('indexPage--white')[0] && getElementOffset(document.getElementsByClassName('indexPage--white')[0]);
		const linker = document.getElementsByClassName('linker')[0].getBoundingClientRect().top;
		const linkBlock = document.getElementsByClassName('frame')[0].getBoundingClientRect().top;

		const elemBlueTop = elemBlue ? elemBlue.top : null;
		const elemWhiteTop = elemWhite ? elemWhite.top : null;
		const mouseColor = document.documentElement.scrollTop;
		if (elemBlue) {
			const isDarkBackground = mouseColor > elemBlueTop;
			this.setState({ color: !isDarkBackground });
		}
		if (elemWhite) {
			const isLightBackground = mouseColor > elemWhiteTop;
			this.setState({ color: isLightBackground });
			if (elemBlue) {
				const isDarkBackground = mouseColor > elemBlueTop;
				this.setState({ color: !isDarkBackground });
			}
		}
		if(linker > linkBlock) {
			this.setState({ colorBottom: false });
		} else {
			const isDarkBackground = mouseColor > elemBlueTop;
			this.setState({ colorBottom: !isDarkBackground });
		}
	}

	showMenu() {
		this.setState({
			showMenu: !this.state.showMenu
		});
		this.state.showMenu ? document.body.classList.remove('fixed') : document.body.classList.add('fixed');
	}

	closeMenu() {
		this.setState({
			showMenu: false
		});
		document.body.classList.remove('fixed');
		setTimeout(() => {
			window.scrollTo(0, 0);
		}, 1000);
	}

	isLoad() {
		let count = 1;
		this.props.countLoading(count++);
		// temporary
		setTimeout(() => {
			this.setState({
				loading: false
			});
		}, 2500);
		// temporary
	}

	render() {
		const { isDark, animationName } = this.props;
		const { mouseX, mouseY, touchClass, clicked, color, showMenu, isHover, isWhiteCursor, colorBottom } = this.state;
		const containerClass = classNames('main', { 'darkTheme': isDark }, animationName);
		const blockZero = isDark || (colorBottom ? !color : true) ? '/assets/images/sign_0w.svg' : '/assets/images/sign_0b.svg';
		const clientWidth = document.body.clientWidth;
		const renderComponent = (
			this.state.loading
				? <Loading/>
				: (
					<main
						className={containerClass}
						onMouseMove={this.onMouseMove.bind(this)}
						onMouseDown={this.onMouseDown.bind(this)}
						onMouseUp={this.onMouseUp.bind(this)}
					>
						<Popup />
						<Cursor
							mouseX={mouseX}
							mouseY={mouseY}
							isHover={isHover}
							className={touchClass}
							clicked={clicked}
							color={isWhiteCursor ? false : showMenu ? false : color}
						/>
						<Header
							isDark={isDark}
							color={color}
							showMenu={showMenu}
							closeMenu={() => this.closeMenu()}
							showMenuFunc={() => this.showMenu()}
						/>
						{clientWidth > 767 && <img className="zeroBlock magnetize" src={blockZero} alt="" />}
						{clientWidth > 767 && <Linker color={!colorBottom ? false : color} />}
						{this.props.children}
					</main>
				)
		);

		return (
			<ParallaxProvider>{renderComponent}</ParallaxProvider>
		);
	}
}

App.propTypes = {
	animationName: PropTypes.string,
	loading: PropTypes.object,
	children: PropTypes.any,
	isDark: PropTypes.bool,
	countLoading: PropTypes.func
};

const mapStateToProps = (state) => ({
	loading: state.loading
});

const mapDispatchToProps = dispatch => ({
	countLoading: bindActionCreators(countLoading, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
